@import
'_custom_bootstrap_vatiables.scss',
'../../bower_components/bootstrap/scss/_functions.scss',
'../../bower_components/bootstrap/scss/_variables.scss',
'../../bower_components/bootstrap/scss/_mixins.scss',
'_param',
'_sprite',
'_sprite_include.scss',
'_message_nav.scss',
'_header',
'_modals.scss';
a.edit{
	&:hover{
		@include sprite($edit_hover);
	}
}

.field_map{
	width: 100%;
	position: fixed;
	top: 71px;
	left: 0;
	height: calc(100vh - 71px);
}
.roll {
    z-index: 10;
    position: absolute;
    bottom: 5px;
    right: 5px;
    width: 225px;
    height: 70px;
    color: #453625;
    font-size: 15px;
    padding: 18px 20px;
    text-align: center;
    background: url(../images/bg_roll.png) no-repeat;
    @media screen and (max-width: 720px) {
    	display: none;
    }
}
.list_infrastructure{
	position: fixed;
	top: 204px;
	left: 5px;
	z-index: 10;
	a{
		display: block;
		width: 50px;
		height: 50px;
		position: relative;
		i{
			position: absolute;
			top: 50%;
			left: 50%;
			transform:translate(-50%,-50%);
		}
		&:hover{
			.ic_1{
				@include sprite($ic_1_hover);
				margin-top: -1px;
			}
			.ic_2{
				@include sprite($ic_2_hover);
				margin-left: -1px;
				margin-top: -1px;
			}
			.ic_3{
				@include sprite($ic_3_hover);

			}
			.ic_4{
				@include sprite($ic_4_hover);

			}
			.ic_5{
				@include sprite($ic_5_hover);
				margin-top: -1px;
			}
			.ic_6{
				@include sprite($ic_6_hover);
				margin-top: -1px;
			}
			.ic_7{
				@include sprite($ic_7_hover);

			}
			.ic_8{
				@include sprite($ic_8_hover);
				margin-left: -1px;
			}
			.ic_9{
				@include sprite($ic_9_hover);
			}
		}
	}
	@media screen and (max-width: 1350px) {
		top: 73px;
	}
	@media screen and (max-width: 480px) {
		a{
			width: 40px;
			height: 40px;
			i{
				transform: scale(.8) translate(-50%, -50%);
			}
		}
	}
}
.user_box{
	width: 122px;
	height: 106px;
	position: fixed;
	top: 80px;
	left: 10px;
	.user_img{
		background: url(../images/bg_user_box.png) no-repeat;
		height: 100%;
		a{
			display: block;
			height: 100%;
			top: 10px;
			left: 17px;
			position: absolute;
			width: 87px;
			height: 87px;
			border-radius: 50%;
			background-size: cover !important;
			background-position: 50% 50%;
		}
	}
	.user_home{
		position: absolute;
		width: 48px;
		height: 45px;
		top: -5px;
		left: 2px;
	}
	.user_emblem{
		position: absolute;
		bottom: -9px;
		left: 5px;
		width: 52px;
		height: 45px;
	}
	@media screen and (max-width: 1350px) {
		display: none;
	}
}
.user_home{
	a{
		&:hover{
			.tower{
				@include sprite($tower_hover);
				margin-top: 1px;
				margin-left: -1px;
			}
		}
	}
}
.user_emblem{
	@include sprite($holder_ic);
	a{
		display: block;
		height: 100%;
		border-radius: 50%;
		width: 36px;
		height: 36px;
		background-size: cover !important;
		background-position: 50% 50%;
		position: relative;
		top: 5px;
		left: 6px;
		opacity: .8;
		&:hover{
			opacity: 1;
		}
	}
}
.close_ico{
	width: 15px;
	height: 15px;
	display: inline-block;
	background: url('data:image/png;base64,') no-repeat;
	background-size: cover;
	opacity: .8;
	&:hover{
		opacity: 1;
	}
}
.alerts_block {
	.alerts_block_cnt{
		
	}
}
.alerts_block{
	position: fixed;
	right: 80px;
	top: 80px;
	width: 270px;
	max-height: calc(100% - 170px) !important;
	max-height: 73% !important;
	// max-height: calc(100vh - 170px) !important;
	border-radius: 5px;
	padding:0 7px;
	.alert_item{
		margin-top: 10px;
		background: url(../images/bg_alert.png) repeat;
		padding: 15px 45px 15px 10px;
		border-radius: 5px;
		position: relative;
		color: #fff;
		text-shadow: 0px 3px 7px rgba(#000,.75);
		box-shadow: 0 0 2px 0px rgba(#000,.75);
		font-size: 14px;
		line-height: 17px;
		&:first-of-type{
			margin-top: 0;
		}
		.close_ico{
			position: absolute;
			top: 15px;
			right: 15px;
		}
		p{
			margin: 0;

		}
	}
	@media screen and (max-width: 1350px) {
		right: 10px;
	}
	@media screen and (max-width: 720px) {
		display: none;
	}
}
.os-host-overflow-y{
	padding-right: 15px !important;
}
.os-host-overflow-x{
	padding-bottom: 15px !important;
}
.os-theme-dark > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle{
	background: #626262 !important;
	opacity: 1;
	z-index: 10;
}
.os-host-overflow-y{
	.os-scrollbar-vertical{
		padding-top: 0;
		padding-bottom: 0;
		&:after{
			content: '';
			position: absolute;
			bottom: 0;
			top: 0;
			left: 50%;
			width: 1px;
			height: 100%;
			background: #d88e2e;
		}
	}
}
.os-host-overflow-x{
	.os-scrollbar-horizontal{
		padding-top: 0;
		padding-bottom: 0;
		height: 6px;
		&:after{
			content: '';
			position: absolute;
			bottom: 0;
			top: 50%;
			left: 50%;
			transform:translateX(-50%);
			width: calc(100% - 4px);
			height: 1px;
			background: #d88e2e;
		}
	}
}
.dangers_list{
	position: fixed;
	top: 75px;
	left: 135px;
	
	z-index: 10;
	li{
		padding: 5px;
		a{
			display: block;
		}
		img{
			display: block;
			max-width: 50px;
			height: auto;
			border-radius: 50%;
		}
	}
	
	@media screen and (max-width: 1350px){
		left: 58px;
		top: 79px;
		.scroll_cnt{
			overflow: auto;
			width: 100%;
			ul{
				max-width: 400px;
			}
		}
		li{
			img{
				max-width: 30px;
			}
		}
	}
	@media screen and (max-width: 530px) {
		right: 10px;
		ul{
			padding-bottom: 10px;
		}
	}
}
.process_list{
	position: fixed;
	bottom:10px;
	left: 0;
	z-index: 10;
	padding-left: 10px !important;
	padding-right: 10px !important;
	li{
		padding-bottom: 20px;
		padding-right: 10px;
		font-size: 0;
		line-height: 0;
	}
	a{
		display: block !important;
	}
	span{
		img{
			display: block;
			max-width: 100%;
			display: block;
			height: auto;
			border-radius: 50%;
			position: relative;
			z-index: 11;
			padding: 5px;
		}
	}
	em{
		bottom: -24px;
		left: 50%;
		transform:translateX(-50%);
		width: 40px;
		height: 40px;
		font-style: normal;
		color: #fff;
		text-align: center;
		i{
			font-style: normal;
			font-size: 10px;
			line-height: 40px;
			display: block;
			font-weight: bold;
		}
	}
	@media screen and (max-width: 1350px){
		bottom: 10px;
		left: 0;
		.list_hldr{
			padding-bottom: 15px;
			position: relative;
			width: 100%;
		}
		li{
			// padding-right: 0;
			transform:scale(.8);
			margin-right: -10px;
			padding-bottom: 10px;
		}
		
	}
	@media screen and (max-width: 530px){
		right: 0;
		.list_hldr{
			overflow: auto;
		}
		ul{
			max-width: 560px;
		}
	}
	@media screen and (max-height: 400px){
		padding-left: 50px !important;
	}
}

.box_test{
	background: #fff;
	position: fixed;
	top: 300px;
	right: 0;
	left: 0;
	z-index: 100;
	padding:40px;
	li{
		border: 1px solid red;
		max-width: 100px;
		flex: 0 0 100px;
		margin-right: 10px;
		height: 100px;
	}
	.gm{
		max-width: 100%;
		overflow: auto;
		position: relative;
	}
}